.modal__title{
	font-size: 1.5rem;
	text-align: center;
	padding-bottom: 5px;
	font-weight: 300;
	border-bottom: solid 1px #eee;
}
.info-label{
	color: gray;
	font-size: .85rem !important;
}

.modal-list-peoples{
	overflow-y: auto;
    max-height: 120px;
}
.modal-list-peoples .item{
	border-bottom: solid 1px #eee;
    padding-bottom: 5px;
    padding-top:    5px;
    font-size: 0.9rem;
}
.formEntrar{
	margin-top: 10px;
}
.formEntrar input{
	background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    border-radius: 0;
    outline: none;
    height: 2.5rem;
    width: 100%;
    font-size: 1rem;
    margin: 0 0 15px 0;
    padding: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}
.formEntrar input[type=text]:focus:not([readonly]){
	border-bottom: 1px solid black;
	-webkit-box-shadow: none;
	        box-shadow: none;
}
.formEntrar .entrar-button{
	width: 100%;
	-webkit-box-shadow: none;
	        box-shadow: none;
	margin-top: 10px;
	background-color: #f44336;
}
.formEntrar .entrar-button:hover{
	-webkit-box-shadow: inset 0px -1px 3px 0 darkred;
	        box-shadow: inset 0px -1px 3px 0 darkred;
}
.formEntrar .entrar-button:active{
	-webkit-box-shadow: inset 0px -3px 5px 0 darkred;
	        box-shadow: inset 0px -3px 5px 0 darkred;
}


