:root {
	--bg-primary-color: #F44336;
    --bg-secondary-color: #2196F3;
}
.scene--menu
{
	background-image: -webkit-gradient(linear, 
		left top, left bottom, 
		from(var(--bg-secondary-color)),
		color-stop(60%, var(--bg-secondary-color)),
		color-stop(60%, var(--bg-primary-color)),
		to(var(--bg-primary-color)));
	background-image: -webkit-linear-gradient(top, 
		var(--bg-secondary-color),
		var(--bg-secondary-color) 60%,
		var(--bg-primary-color) 60%,
		var(--bg-primary-color) 100%);
	background-image: -o-linear-gradient(top, 
		var(--bg-secondary-color),
		var(--bg-secondary-color) 60%,
		var(--bg-primary-color) 60%,
		var(--bg-primary-color) 100%);
	background-image: linear-gradient(to bottom, 
		var(--bg-secondary-color),
		var(--bg-secondary-color) 60%,
		var(--bg-primary-color) 60%,
		var(--bg-primary-color) 100%);
	background-repeat: no-repeat;
	background-size: 100%;
	height: 100%;
	min-height: 100vh;
}
.scene--menu .foot{
	padding: 5px;
	color: white;
	text-align: center;
	font-size: 1.1rem;
}


